// first-screen
.first-screen {
  position:relative;
  background-color: #18182A;
  width:100%;
  min-height:100vh;
  display: flex;
  flex-wrap:wrap;
  align-items: center;
  background-image: url('../img/bg.png');
  background-repeat:no-repeat;
  background-position: right center;
  background-size: contain;

  @media(max-width:600px){
    background-size:contain;
    background-position: center bottom;
    align-items: flex-start;
    padding-top:92px;
  }

  & h1 {
    color:#ffffff;
    font-weight:300;
    font-family:'SBSansDisplay';
    font-size:7.5rem;

    & span {
      display:block;
      font-weight:400;
    }
    @media (max-width:1920px) and (min-width: 1367px){
      font-size:6.5rem;
    }
    @media (max-width:1367px) and (min-width: 600px){
      font-size:5.5rem;
    }
    @media (max-width: 600px)and(min-width:330px){
      font-size: 5rem;
    }
    @media (max-width: 330px){
      font-size: 3.5rem;
    }
    

  }

}

.two-screen {
  position:relative;
  z-index: 3; 
  padding-top:60px;
  margin-bottom:100px;
  @media (max-width:820px){
    padding-top:2rem;
    margin-bottom:30px;
  }
}
.block-quote {
  position:relative;
  display:flex;
  display: -webkit-flex;
  flex-wrap:wrap;
  align-items: flex-end;
  width:80%;
  max-width: 1270px;
  margin: 0 auto;
  padding:50px 80px 0 80px;
  background: rgba(239, 239, 238, 0.4);
  border-radius: 20px;
  @media(min-width:1920px){
    padding:80px 130px 0 130px;
  }
  @media(max-width:768px){
   width:100%;
   padding: 30px 20px 0 30px;
   flex-wrap: wrap-reverse;
   flex-direction: column-reverse;
   flex: 1;
  }

  &:before {
    content:'';
    position: absolute;
    left: -30%;
    top: -20%;
    width: 150%;
    height: 150%;
    background-image: url(../img/block-quote-gradient.png);
    background-repeat: no-repeat;
    z-index: 0;
    background-size: 50%;
    @media(max-width:768px){
      top:0;
    }
    @media(max-width:600px){
      background-size: 100%;
    }
  }
  &__content {
    margin-bottom:30px;
    position: relative;
    @media (max-width:600px){
      order:3;
      padding-top:2rem;
    }
  }



  &__img {
    position: relative;
    z-index: 5;
    & img {
      min-width:100%;
      margin-bottom: -6px;
    }
    @media (max-width:1180px){
      order:1;
      min-width:100%;
      & img {
        max-width:200px;
        min-width:200px;
      }
    }
  }

  & p {
    flex-basis: 100%;
  }
  &__name {
    font-weight:600;
    margin-bottom:5px;
    font-size:1.15rem;
  }
  &__position {
     color: #949494;
     font-size: 12px;
     max-width:50%;
  }
  &__read-more {
    color: #b3adad;
    
    & span {
      text-decoration:underline;
    }

    &:after {
      content:'*';
      text-decoration:none;

    }
  }
}
.three-screen {
  padding:100px 0;
  @media(max-width:820px){
    padding:60px 0 0 0;
  }
}
.info-card {
  display: flex;
  align-items: stretch;
  &:first-child {
    position:relative;
    &:before {
      content:'';
      width:130%;
      height:110%;
      position:absolute;
      left:0;
      top:0;
    background: rgb(24,239,242);
    background: radial-gradient(circle,#18eff2 2%,rgba(255,255,255,0) 44%);
    filter: blur(60px);
    }
  }
 
  &__wrapper {
    position:relative;
    width:100%;
    background: rgba(239, 238, 238, 0.4);
    border-radius: 10px;
    margin-bottom:2rem;
    padding:2rem 2.2rem;
  }

  &__header {
    font-size:54px;
    font-size:3.87rem;
    line-height:6rem;
    margin-bottom:1.5rem;
    background: linear-gradient(to right,#09a552 0,#333f48 21%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    color: #219F38;
  }
}
.four-screen {
  padding:100px 0;
 
  @media(max-width:820px){
      padding:70px 0 0 0;
  }
  &__content {
    width:80%;
    margin:0 auto;
    margin-bottom:100px;
    @media(max-width:768px){
      width:100%;
      margin-bottom:30px;
    }
  
  }
  & h3 {
    display: block;
    margin: 0 auto;
    text-align: center;
    font-family: $font-family--alt;
    font-size: 72px;
    background: linear-gradient(to right,#09a552 0,#333f48 81%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    color: #333f48;
    margin-bottom:55px;
    & span {
      display:block;
    }
    @media(max-width:768px) and(min-width:600px){
      font-size: 52px;
      margin-bottom:35px;
    }
    @media(max-width:600px){
      font-size: 30px;
      text-align: left;
      margin-bottom:35px;
    }
  }
}
.partners{
  @media(max-width:768px){
    margin-top:90px;
    & h3 {
      margin-bottom:35px;
    }
  }
  &__list {
     & img {
      width:70%;
      max-width:176px;
      filter:grayscale(1);
      opacity:0.8;

      &:hover {
        filter:grayscale(0);
        opacity:1
      }
    }
  }
  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.six-screen {
  padding:120px 0;
  background-image: url('../img/six-screen-bg.jpg');
  background-repeat:no-repeat;
  background-position:top 200px right;
  @media(max-width:820px){
    padding:120px 0 0px 0;
  }
  @media(max-width:600px){
    background-position:top -50px center;
    background-size: 200%;
  }
  &__content { 
    position:relative;
    width:80%;
    margin:0 auto;
    margin-bottom:100px; 
    @media(max-width:768px){
      width:100%;
      margin-bottom:40px;
      margin-top:40px;
    }

    & img {
      display:block;
      margin:0 auto;
      border-radius: 20px;
      margin-bottom:45px;
      position:relative;
      width: 100%;
      
 
    }
  }
  & h3 {
    display: block;
    margin: 0 auto;
    text-align: center;
    font-family: $font-family--alt;
    font-size: 72px;
    background: linear-gradient(to right,#09a552 0,#333f48 61%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    color: #333f48;
    margin-bottom:55px;
    & span {
      display:block;
    }
    @media(max-width:768px) and(min-width:600px){
      font-size: 52px;
    }
    @media(max-width:600px){
      font-size: 30px;
      text-align: left;
      margin-bottom:35px;
    }
  }
  &__readmore {
    position:absolute;
    right:0;
    bottom:0;
    padding:10px 40px;
    @media(max-width:600px) {
     display:block;
     position:relative
    }
  }
  &__more {
    height:0;
    max-height:0;
    overflow:hidden;
    transition:all 0.5s linear;

    &--open {
      height:auto;
      max-height:1000px;
      transition:all 0.5s linear;
      @media(max-width:768px){
        max-height:2000px;
      }
    }
  }
}
.seven-screen {
  background-image: url('../img/scientific_prize.png');
  background-size: cover;
  color:#ffffff;
  padding-bottom:120px;
  background-position: top center;

  & h3 {
    padding:180px 0;
    color:#ffffff;
    font-weight:600;
    font-family:'SBSansDisplay';
    font-size:7.5rem;
    display:block;
    text-align: center;
    @media(max-width:768px){
      padding: 100px 0;
    }

    & span {
      display:block;

      font-weight:300;
    }
    @media (max-width:1920px) and (min-width: 1367px){
      font-size:6.5rem;
    }
    @media (max-width:1367px) and (min-width: 600px){
