  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='12' height='7' viewBox='0 0 12 7' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cline x1='0.353553' y1='0.646447' x2='6.35355' y2='6.64645' stroke='%232D3032'/%3E%3Cline y1='-0.5' x2='8.48528' y2='-0.5' transform='matrix(-0.707107 0.707107 0.707107 0.707107 12 1)' stroke='%232D3032'/%3E%3C/svg%3E");
    background-repeat:no-repeat;
    background-position: right 22px top 22px;
    margin-bottom:20px;

    &.active {
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='12' height='7' viewBox='0 0 12 7' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cline x1='11.6464' y1='6.35355' x2='5.64645' y2='0.353553' stroke='%2321A038'/%3E%3Cline y1='-0.5' x2='8.48528' y2='-0.5' transform='matrix(0.707107 -0.707107 -0.707107 -0.707107 0 6)' stroke='%2321A038'/%3E%3C/svg%3E");

    }

    & input {
      position:absolute;
      top:0;
      left:0;
      padding: 0 50px 0 15px;
      margin: 0;
      height:100%;
      width: 100%;
      font-family: 'SBSansText';
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='18' height='18' viewBox='0 0 18 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cline x1='13.0607' y1='13' x2='17' y2='16.9393' stroke='%232D3032' stroke-width='1.5' stroke-linecap='round'/%3E%3Ccircle cx='7.5' cy='7.5' r='6.75' stroke='%232D3032' stroke-width='1.5' stroke-linecap='round'/%3E%3C/svg%3E");
      background-repeat:no-repeat;
      background-position: right 15px center;
      transition:all 0.3s linear;
      border: 1px solid #676767;
      border-radius:5px;
      &:focus, &.active {
        background-image:none;
        
        &:-moz-placeholder {
          opacity:0;
      }
      &::-webkit-input-placeholder {
       opacity:0;
    }

      }
    }

    &__close {
    position:absolute;
    width:20px;
    height:10px;
    right: 25px;
    top: 20px;
    transform:rotate(45deg);
    border-bottom:1px solid #21A038;
    display:none;
    &:after {
      content:'';
      position:absolute;
      left:-10px;
      top:0;
      width:20px;
      height:20px;
      border-bottom:1px solid #21A038;
      transform:rotate(-90deg);
    }
    &:hover {
      cursor:pointer;
    }
    &.active {
      display:block;
    }
    }
}
.dropdown .select {
    cursor: pointer;
    display: block;
    padding: 14px 11px;
    border-radius:5px;
    height:51px;
    color: #2D3032;
    border: 1px solid #676767;

    & span {
      color:rgb(117, 117, 117);
    }
}

.dropdown .dropdown-menu {
    position: absolute;
    background-color: #fff;
    width: 100%;
    left: 0;
    margin-top: -3px;
    overflow: hidden;
    padding-top:1rem;
    display: none;
    overflow-y: auto;
    z-index: 9;
    border: 1px solid #676767;
    border-top:0px;
    border-bottom-left-radius:5px;
    border-bottom-right-radius:5px;

    &:before {
      content:'';
      display:block;
      width:90%;
      height:1px;
      margin:0 auto;
      margin-top: -10px;
      background-color:#E0E0E0;
    }
}
.dropdown .dropdown-menu ul{
  max-height:232px;
  overflow-y: scroll;
  scrollbar-width: thin;          
  scrollbar-color: #9e9e9e #F5F7FA;  
}
.dropdown-menu ul::-webkit-scrollbar {
  width: 7px;               /* width of the entire scrollbar */
}

.dropdown-menu ul::-webkit-scrollbar-track {
  background: #F5F7FA;        /* color of the tracking area */
}

.dropdown-menu ul::-webkit-scrollbar-thumb {
  background-color: #9e9e9e;    /* color of the scroll thumb */
  border-radius: 0px;       /* roundness of the scroll thumb */
  border: 7px solid #9e9e9e;  /* creates padding around scroll thumb */
}

.dropdown .dropdown-menu ul li {
    padding: 15px;
    transition: all .2s ease-in-out;
    cursor: pointer
} 
.dropdown .dropdown-menu ul{
    padding: 0;
    list-style: none
}
